@import "../colors";

#limits {
  .fill {
    grid-area: 1 / 3 / 15/ 21;
    border-bottom-left-radius: 4rem;
  }

  .img {
    background-image: url(./assets/clone-masked.png);
    grid-area: 1 / 11 / 21 / 20;
    background-size: cover;
  }

  .quotes {
    font-family: Montserrat, sans-serif;
    font-weight: bold;
    grid-area: 7/4/15/10;
    color: $rich-black;

    > blockquote {
      background-color: transparent;
      font-weight: bold;
      padding: 0 0 0 2rem;
      border-left: solid .5rem;
    }
  }


  h1 {
    transform-origin: 0 100%;
    transform: rotate(-90deg);
    grid-area: 13/18/18/20;
    //vertical-align: bottom;
    align-self: end;
    font-size: 6rem;
  }
}