@import "../colors";

#russia {
  .fill {
    grid-area: 1/1/14/18;
    border-bottom-right-radius: 4rem;
  }

  .img {
    background-image: url(./assets/russia2077-masked.png);
    background-position: left center;
    background-size: contain;
    background-repeat: no-repeat;
    grid-area: 1/1/21/16;
  }

  h1 {
    grid-area: 6/10/8/17;
    font-size: 6rem;
    text-align: right;
    color: $rich-black
  }

  .quotes {
    grid-area: 8/9/21/16;
  }
}